
























import Vue from "vue";
import { Component } from "vue-property-decorator";
import dayjs from "dayjs";
import clone from "@/lib/clone";

import Tabs from "@/components/Tabs.vue";

import typeList from "@/constants/typeList.ts";

@Component({
  components: {
    Tabs
  }
})
export default class Statistics extends Vue {
  tagString(tags: Tag[]) {
    const tagName = [];
    for (let i = 0; i < tags.length; i++) {
      tagName.push(tags[i].name);
    }
    return tags.length === 0 ? "无" : tagName.join(",");
  }
  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return "今天";
    } else if (day.isSame(now.valueOf() - 86400 * 1000, "day")) {
      return "昨天";
    } else if (day.isSame(now.valueOf() - 86400 * 2000, "day")) {
      return "前天";
    } else {
      return day.format("YYYY年M月D日");
    }
  }

  get TypeName() {
    return this.type === "-" ? "支出" : "收入";
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const recordList = this.recordList;
    if (recordList.length === 0) {
      return [];
    } else if (this.type !== recordList[0].type && recordList.length === 1) {
      return [];
    }
    const newList = clone(recordList)
      .filter(r => r.type === this.type) // 使得支出和收入与Tab一致
      .sort(
        (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
      );
    type Result = {
      title: string;
      total?: number;
      items: RecordItem[];
    }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt).format("YYYY-MM-DD"),
        items: [newList[0]]
      }
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt).format("YYYY-MM-DD"),
          items: [current]
        });
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  get typeWord() {
    return this.type === "-" ? "总支出" : "总收入";
  }
  type = "-";
  typeList = typeList;

  created() {
    this.$store.commit("fetchRecords");
  }
}
